.form-block {
  background: #dbe2ea;
  padding-top: 5%;

  a[href] {
    color: #0880ae;
    text-decoration: none;
  }

  .btn-large {
    button {
      background: #0880ae;
      border-radius: 6px;
      font-size: 16px;
      line-height: 21px;
      text-align: center;
      color: #ebf4f8;
      padding: 15px;
      border: 2px solid transparent;
      width: 100%;
      transition: box-shadow 0.1s;

      &:hover:not(:disabled) {
        box-shadow: 0px 12px 24px rgba(44, 39, 56, 0.08), 0px 24px 48px rgba(44, 39, 56, 0.16);
      }
      &:active {
        border: 2px solid rgba(44, 39, 56, 0.86);
        box-shadow: 0px 2px 4px rgba(44, 39, 56, 0.0001), 0px 4px 8px rgba(44, 39, 56, 0.08) !important;
      }
      &:disabled {
        background: #dbe2ea;
        color: #2c2738;
      }
    }
  }

  .text-field {
    .error {
      font-size: 14px;
      color: #ff7171;
      display: block;
      min-height: 21px;
      margin-bottom: 8px;
    }
    input:not([type="checkbox"]) {
      border: 1px solid #dbe2ea;
      border-radius: 6px;
      padding: 15px;
      margin: 8px 0;
      width: 100%;
      box-shadow: 0px 4px 8px rgba(44, 39, 56, 0.04);

      &::placeholder {
        color: #7c9cbf;
      }
    }
  }

  .label {
    color: #756f86;
  }

  .custom-check-box {
    display: flex;
    align-items: center;
    margin: 35px 0 8px;
    .check-box {
      width: 28px;
      height: 28px;
      position: relative;
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
      cursor: pointer;

      &:not(:checked)::after {
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        border: 1px solid #dbe2ea;
        border-radius: 4px;
      }
      &:checked::before {
        content: "";
        position: absolute;
        background-image: url(../icons/checked.svg);
        background-repeat: no-repeat;
        background-position: 50%;
        background-color: #fff;
        border: 2px solid #0880ae;
        width: 100%;
        height: 100%;
        border-radius: 4px;
      }
    }
    label {
      margin-left: 5px;
    }
  }

  .custom-select {
    position: relative;

    ul {
      position: absolute;
      z-index: 1;
      background: #fff;
      border: 1px solid #dbe2ea;
      border-radius: 6px;
      width: 100%;
      padding: 0;
      margin-top: 4px;
      cursor: pointer;

      li {
        padding: 15px;
        &:hover,
        &.active {
          background-color: #ebf4f8;
        }
      }
    }
    .value {
      width: 100%;
      padding: 15px;
      border: 1px solid #dbe2ea;
      border-radius: 6px;
      cursor: pointer;
      background-repeat: no-repeat;
      background-position: 98% 50%;
      background-image: url(../icons/arrow.svg);

      &.open {
        border: 2px solid #0880ae;
      }
    }
  }

  form {
    max-width: 480px;
    margin: auto;
    padding: 40px 30px;
    background: #fff;
    border-radius: 24px;

    .form-block__top {
      margin-bottom: 60px;
      h1 {
        font-size: 34px;
        font-weight: 700;
      }
    }
  }
}
